import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { BeatMap } from './BeatMap';
import { 
  RefreshCw, 
  AlertCircle, 
  MapPin, 
  Activity, 
  Calendar, 
  Eye, 
  Footprints, 
  AlertTriangle,
  ChevronRight,
  Loader2,
  Info,
  TrendingUp,
  TrendingDown,
  Map
} from 'lucide-react';
import {
  LineChart,
  Line,
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts';
import { 
  fetchDashboardKPIs, 
  fetchMonthlyTrends, 
  fetchDivisionStats,
  fetchRecentObservations
} from '@/lib/dashboardService';
import type { 
  DashboardKPIs, 
  MonthlyTrend, 
  DivisionStat,
  RecentObservation 
} from '@/lib/dashboardService';
import { Badge } from "@/components/ui/badge";
import { supabase } from '@/lib/supabaseClient';
import { Skeleton } from "@/components/ui/skeleton";
import { Tooltip as UITooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { toast } from "react-hot-toast";
import { Calendar as CalendarIcon } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { format } from "date-fns";
import { cn } from "@/lib/utils";
import { useToast } from "@/components/ui/use-toast";
import { DatePicker } from "@/components/ui/date-picker";

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884d8'];

interface FilterOption {
  id: string;
  name: string;
  new_id?: string;
}

interface KPI {
  title: string;
  value: number;
  change: number;
  trend: 'up' | 'down';
}

interface Observation {
  id: string;
  division: string;
  range: string;
  date: string;
  type: string;
  count: number;
  status: string;
}

export const EnhancedDashboard: React.FC = () => {
  const [activeTab, setActiveTab] = useState('overview');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [kpis, setKpis] = useState<KPI[]>([]);
  const [monthlyTrends, setMonthlyTrends] = useState<MonthlyTrend[]>([]);
  const [divisionStats, setDivisionStats] = useState<DivisionStat[]>([]);
  const [recentObservations, setRecentObservations] = useState<Observation[]>([]);
  const [divisions, setDivisions] = useState<string[]>([]);
  const [ranges, setRanges] = useState<string[]>([]);
  const [beats, setBeats] = useState<FilterOption[]>([]);
  const [selectedDivision, setSelectedDivision] = useState<string>('all');
  const [selectedRange, setSelectedRange] = useState<string>('all');
  const [selectedBeat, setSelectedBeat] = useState('');
  const [mapLoading, setMapLoading] = useState(false);
  const [refreshing, setRefreshing] = useState(false);
  const [filteredStats, setFilteredStats] = useState<DashboardKPIs | null>(null);
  const [date, setDate] = useState<Date>(new Date());
  const { toast } = useToast();

  const fetchDashboardData = async () => {
    try {
      setRefreshing(true);
      setError(null);
      const [kpiData, trendsData, statsData, observationsData] = await Promise.all([
        fetchDashboardKPIs(),
        fetchMonthlyTrends(),
        fetchDivisionStats(),
        fetchRecentObservations()
      ]);

      // Convert KPI data to the expected format
      const kpis: KPI[] = [
        {
          title: 'Total Observations',
          value: kpiData.total_observations || 0,
          change: 0, // TODO: Calculate change from previous period
          trend: 'up'
        },
        {
          title: 'Active Divisions',
          value: kpiData.total_observations > 0 ? 1 : 0, // Simplified for now
          change: 0,
          trend: 'up'
        },
        {
          title: 'Total Elephants',
          value: kpiData.total_elephants || 0,
          change: 0,
          trend: 'up'
        }
      ];

      setKpis(kpis);
      setMonthlyTrends(trendsData);
      setDivisionStats(statsData);
      
      // Convert observations to the expected format
      const formattedObservations: Observation[] = observationsData.map(obs => ({
        id: obs.associated_division + '_' + obs.activity_date, // Create a unique ID
        division: obs.associated_division,
        range: obs.associated_range,
        date: obs.activity_date,
        type: obs.observation_type,
        count: obs.total_elephants || 0,
        status: 'active'
      }));
      
      setRecentObservations(formattedObservations);
    } catch (err) {
      setError('Failed to fetch dashboard data');
      console.error('Dashboard data fetch error:', err);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  const fetchFilteredStats = async () => {
    try {
      setMapLoading(true);
      let query = supabase
        .from('activity_observation')
        .select('*');

      if (selectedBeat && selectedBeat !== 'all') {
        query = query.eq('associated_beat', selectedBeat);
      } else if (selectedRange && selectedRange !== 'all') {
        query = query.eq('associated_range', selectedRange);
      } else if (selectedDivision && selectedDivision !== 'all') {
        query = query.eq('associated_division', selectedDivision);
      }

      const { data, error } = await query;
      
      if (error) {
        console.error('Error fetching filtered stats:', error);
        return;
      }

      if (!data) {
        console.log('No data returned from query');
        return;
      }

      const stats: DashboardKPIs = {
        total_observations: data.length,
        direct_sightings: data.filter(obs => obs.observation_type === 'Direct Sighting').length,
        indirect_sightings: data.filter(obs => obs.observation_type === 'Indirect Signs').length,
        total_loss: data.filter(obs => obs.observation_type === 'Loss Report').length,
        today_observations: data.filter(obs => new Date(obs.activity_date).toDateString() === new Date().toDateString()).length,
        today_direct_sightings: data.filter(obs => 
          obs.observation_type === 'Direct Sighting' && 
          new Date(obs.activity_date).toDateString() === new Date().toDateString()
        ).length,
        today_indirect_sightings: data.filter(obs => 
          obs.observation_type === 'Indirect Signs' && 
          new Date(obs.activity_date).toDateString() === new Date().toDateString()
        ).length,
        today_loss: data.filter(obs => 
          obs.observation_type === 'Loss Report' && 
          new Date(obs.activity_date).toDateString() === new Date().toDateString()
        ).length,
        total_elephants: data.reduce((sum, obs) => sum + (obs.total_elephants || 0), 0),
        male_elephants: data.reduce((sum, obs) => sum + (obs.male_elephants || 0), 0),
        female_elephants: data.reduce((sum, obs) => sum + (obs.female_elephants || 0), 0),
        calves: data.reduce((sum, obs) => sum + (obs.calves || 0), 0),
        unknown_elephants: data.reduce((sum, obs) => sum + (obs.unknown_elephants || 0), 0),
        last_updated: new Date().toISOString()
      };

      setFilteredStats(stats);
    } catch (err) {
      console.error('Error in fetchFilteredStats:', err);
    } finally {
      setMapLoading(false);
    }
  };

  useEffect(() => {
    fetchDashboardData();
  }, [date, selectedDivision, selectedRange]);

  // Fetch divisions
  useEffect(() => {
    async function fetchDivisions() {
      try {
        const { data, error } = await supabase
          .from('divisions')
          .select('name')
          .order('name');
        
        if (error) throw error;
        setDivisions(data.map(d => d.name));
      } catch (err) {
        console.error('Error fetching divisions:', err);
      }
    }
    fetchDivisions();
    if (selectedDivision) {
      fetchFilteredStats();
    }
  }, [selectedDivision]);

  // Fetch ranges when division is selected
  useEffect(() => {
    async function fetchRanges() {
      if (!selectedDivision || selectedDivision === 'all') {
        setRanges([]);
        return;
      }
      try {
        setMapLoading(true);
        const { data: rangesData, error: rangesError } = await supabase
          .from('ranges')
          .select('name')
          .eq('division_name', selectedDivision)
          .order('name');
      
        if (rangesError) {
          console.error('Error fetching ranges:', rangesError);
          throw rangesError;
        }
        setRanges(rangesData.map(r => r.name));
      } catch (err) {
        console.error('Error fetching ranges:', err);
      } finally {
        setMapLoading(false);
      }
    }
    fetchRanges();
    if (selectedRange) {
      fetchFilteredStats();
    }
  }, [selectedDivision]);

  // Fetch beats when range is selected
  useEffect(() => {
    async function fetchBeats() {
      if (!selectedRange || selectedRange === 'all') {
        setBeats([]);
        return;
      }
      try {
        setMapLoading(true);
        const { data, error } = await supabase
          .from('beats')
          .select('name')
          .eq('range_name', selectedRange)
          .order('name');
      
        if (error) {
          console.error('Error fetching beats:', error);
          throw error;
        }
        setBeats(data.map(beat => ({ id: beat.name, name: beat.name })));
      } catch (err) {
        console.error('Error fetching beats:', err);
      } finally {
        setMapLoading(false);
      }
    }
    fetchBeats();
    if (selectedBeat) {
      fetchFilteredStats();
    }
  }, [selectedRange]);

  const handleDivisionChange = (value: string) => {
    setSelectedDivision(value);
    setSelectedRange('');
    setSelectedBeat('');
  };

  const handleRangeChange = (value: string) => {
    setSelectedRange(value);
    setSelectedBeat('');
  };

  const handleRefresh = async () => {
    setRefreshing(true);
    await fetchDashboardData();
    setRefreshing(false);
    toast({
      title: "Success",
      description: "Dashboard data refreshed successfully.",
    });
  };

  if (loading) {
    return (
      <div className="space-y-6 p-6 max-w-[1400px] mx-auto">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Dashboard</h1>
          <Skeleton className="h-10 w-24" />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {[...Array(4)].map((_, i) => (
            <Card key={i} className="hover:shadow-md transition-shadow">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <Skeleton className="h-4 w-32" />
                <Skeleton className="h-4 w-4" />
              </CardHeader>
              <CardContent>
                <Skeleton className="h-8 w-16 mb-2" />
                <Skeleton className="h-4 w-24" />
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] space-y-4">
        <AlertCircle className="h-12 w-12 text-destructive" />
        <div className="text-center">
          <h2 className="text-xl font-semibold text-destructive mb-2">Error Loading Dashboard</h2>
          <p className="text-muted-foreground mb-4">{error}</p>
          <Button onClick={fetchDashboardData} variant="outline" className="gap-2">
            <RefreshCw className="h-4 w-4" />
            Try Again
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6 p-6 max-w-[1400px] mx-auto">
      <div>
        <h1 className="text-3xl font-bold">Dashboard</h1>
        <p className="text-muted-foreground mt-1">Monitor elephant activities and observations</p>
      </div>
      {/* Filters Section - all in a single flex row */}
      <div className="flex flex-col md:flex-row gap-4 items-center justify-between mt-6">
        <div className="flex flex-col sm:flex-row gap-4 w-full">
          {/* Date Picker - use reusable DatePicker component */}
          <DatePicker 
            date={date} 
            setDate={(newDate: Date | null) => {
              if (newDate) {
                setDate(newDate);
              }
            }} 
          />
          {/* Division Selector */}
          <Select value={selectedDivision} onValueChange={handleDivisionChange}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Select Division" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Divisions</SelectItem>
              {divisions.map((division) => (
                <SelectItem key={division} value={division}>
                  {division}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
          {/* Range Selector */}
          <Select value={selectedRange} onValueChange={handleRangeChange}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Select Range" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Ranges</SelectItem>
              {ranges.map((range) => (
                <SelectItem key={range} value={range}>
                  {range}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <Button 
          variant="outline" 
          size="sm" 
          onClick={handleRefresh}
          disabled={refreshing}
          className="w-full md:w-auto"
        >
          {refreshing ? (
            <Loader2 className="h-4 w-4 mr-2 animate-spin" />
          ) : (
            <RefreshCw className="h-4 w-4 mr-2" />
          )}
          Refresh Data
        </Button>
      </div>

      <Tabs defaultValue="overview" className="space-y-6">
        <TabsList className="grid w-full grid-cols-3 lg:w-[400px]">
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="activity">Activity</TabsTrigger>
          <TabsTrigger value="map">Map View</TabsTrigger>
        </TabsList>

        <TabsContent value="overview" className="space-y-6">
          {/* KPI Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {kpis.map((kpi, index) => (
              <Card key={index} className="bg-white shadow-sm hover:shadow-md transition-shadow">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium text-gray-600">
                    {kpi.title}
                  </CardTitle>
                  {kpi.trend === 'up' ? (
                    <TrendingUp className="h-4 w-4 text-green-500" />
                  ) : (
                    <TrendingDown className="h-4 w-4 text-red-500" />
                  )}
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{kpi.value.toLocaleString()}</div>
                  <p className={cn(
                    "text-xs mt-1",
                    kpi.trend === 'up' ? "text-green-500" : "text-red-500"
                  )}>
                    {kpi.trend === 'up' ? '+' : ''}{kpi.change}% from last period
                  </p>
                </CardContent>
              </Card>
            ))}
          </div>

          {/* Charts Section */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <Card className="hover:shadow-md transition-shadow">
              <CardHeader>
                <CardTitle className="text-lg font-semibold">Monthly Activity Trends</CardTitle>
                <CardDescription>Track elephant activity patterns over time</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="h-[300px]">
                  <ResponsiveContainer width="100%" height="100%">
                    <LineChart data={monthlyTrends}>
                      <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                      <XAxis dataKey="month" className="text-muted-foreground text-xs" />
                      <YAxis className="text-muted-foreground text-xs" />
                      <Tooltip 
                        contentStyle={{ 
                          backgroundColor: 'hsl(var(--card))',
                          borderColor: 'hsl(var(--border))',
                          borderRadius: 'var(--radius)'
                        }}
                      />
                      <Legend />
                      <Line 
                        type="monotone" 
                        dataKey="total_observations" 
                        stroke="hsl(var(--primary))" 
                        name="Total Observations"
                        strokeWidth={2}
                      />
                      <Line 
                        type="monotone" 
                        dataKey="direct_sightings" 
                        stroke="hsl(var(--secondary))" 
                        name="Direct Sightings"
                        strokeWidth={2}
                      />
                      <Line 
                        type="monotone" 
                        dataKey="indirect_sightings" 
                        stroke="hsl(var(--accent))" 
                        name="Indirect Signs"
                        strokeWidth={2}
                      />
                      <Line 
                        type="monotone" 
                        dataKey="loss_reports" 
                        stroke="hsl(var(--destructive))" 
                        name="Loss Reports"
                        strokeWidth={2}
                      />
                    </LineChart>
                  </ResponsiveContainer>
                </div>
              </CardContent>
            </Card>

            <Card className="hover:shadow-md transition-shadow">
              <CardHeader>
                <CardTitle className="text-lg font-semibold">Division-wise Sightings</CardTitle>
                <CardDescription>Distribution of elephant activities across divisions</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="h-[300px]">
                  <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={divisionStats}>
                      <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                      <XAxis 
                        dataKey="associated_division" 
                        className="text-muted-foreground text-xs"
                        angle={-45}
                        textAnchor="end"
                        height={60}
                      />
                      <YAxis className="text-muted-foreground text-xs" />
                      <Tooltip 
                        contentStyle={{ 
                          backgroundColor: 'hsl(var(--card))',
                          borderColor: 'hsl(var(--border))',
                          borderRadius: 'var(--radius)'
                        }}
                      />
                      <Legend />
                      <Bar 
                        dataKey="direct_sightings" 
                        name="Direct Sightings" 
                        fill={COLORS[0]}
                        stackId="stack"
                      />
                      <Bar 
                        dataKey="indirect_sightings" 
                        name="Indirect Signs" 
                        fill={COLORS[1]}
                        stackId="stack"
                      />
                      <Bar 
                        dataKey="loss_reports" 
                        name="Loss Reports" 
                        fill={COLORS[2]}
                        stackId="stack"
                      />
                    </BarChart>
                  </ResponsiveContainer>
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="activity" className="space-y-6">
          {/* Activity Summary Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <Card className="hover:shadow-md transition-shadow">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Today's Observations</CardTitle>
                <Calendar className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{filteredStats?.today_observations || 0}</div>
                <div className="flex items-center gap-2 mt-1">
                  <Badge variant="outline" className="text-xs">
                    {new Date().toLocaleDateString()}
                  </Badge>
                </div>
              </CardContent>
            </Card>

            <Card className="hover:shadow-md transition-shadow">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Direct Sightings Today</CardTitle>
                <Eye className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{filteredStats?.today_direct_sightings || 0}</div>
                <div className="flex items-center gap-2 mt-1">
                  <Badge variant="outline" className="text-xs">
                    {((filteredStats?.today_direct_sightings || 0) / (filteredStats?.today_observations || 1) * 100).toFixed(0)}% of total
                  </Badge>
                </div>
              </CardContent>
            </Card>

            <Card className="hover:shadow-md transition-shadow">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Indirect Signs Today</CardTitle>
                <Footprints className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{filteredStats?.today_indirect_sightings || 0}</div>
                <div className="flex items-center gap-2 mt-1">
                  <Badge variant="outline" className="text-xs">
                    {((filteredStats?.today_indirect_sightings || 0) / (filteredStats?.today_observations || 1) * 100).toFixed(0)}% of total
                  </Badge>
                </div>
              </CardContent>
            </Card>

            <Card className="hover:shadow-md transition-shadow">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Loss Reports Today</CardTitle>
                <AlertTriangle className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{filteredStats?.today_loss || 0}</div>
                <div className="flex items-center gap-2 mt-1">
                  <Badge variant="outline" className="text-xs">
                    {((filteredStats?.today_loss || 0) / (filteredStats?.today_observations || 1) * 100).toFixed(0)}% of total
                  </Badge>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Recent Observations Table */}
          <Card className="hover:shadow-md transition-shadow">
            <CardHeader className="flex flex-row items-center justify-between">
              <div>
                <CardTitle className="text-lg font-semibold">Recent Observations</CardTitle>
                <CardDescription>
                  Showing last {recentObservations.length} observations across all divisions
                </CardDescription>
              </div>
              <Button 
                variant="outline" 
                size="sm" 
                onClick={fetchDashboardData} 
                className="gap-2"
                disabled={refreshing}
              >
                {refreshing ? (
                  <Loader2 className="h-4 w-4 animate-spin" />
                ) : (
                  <RefreshCw className="h-4 w-4" />
                )}
                Refresh
              </Button>
            </CardHeader>
            <CardContent>
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b">
                      <th className="text-left p-3 text-muted-foreground font-medium">Location</th>
                      <th className="text-left p-3 text-muted-foreground font-medium">Date</th>
                      <th className="text-left p-3 text-muted-foreground font-medium">Type</th>
                      <th className="text-right p-3 text-muted-foreground font-medium">Elephants</th>
                    </tr>
                  </thead>
                  <tbody>
                    {recentObservations.map((observation, index) => (
                      <tr key={index} className="border-b hover:bg-muted/50 transition-colors">
                        <td className="p-3">
                          <div className="font-medium">{observation.division}</div>
                          <div className="text-sm text-muted-foreground">
                            {observation.range}
                          </div>
                        </td>
                        <td className="p-3">
                          {new Date(observation.date).toLocaleDateString()}
                        </td>
                        <td className="p-3">
                          <Badge 
                            variant={
                              observation.type === 'Direct Sighting' ? 'default' :
                              observation.type === 'Indirect Signs' ? 'secondary' :
                              'destructive'
                            }
                          >
                            {observation.type}
                          </Badge>
                        </td>
                        <td className="p-3 text-right font-medium">
                          {observation.count}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="map" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="text-lg font-semibold">Area Selection</CardTitle>
              <CardDescription>Select a division, range, or beat to view on the map</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-2">Division</label>
                  <Select
                    value={selectedDivision}
                    onValueChange={handleDivisionChange}
                    disabled={mapLoading}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select Division" />
                    </SelectTrigger>
                    <SelectContent>
                      {divisions.map((division) => (
                        <SelectItem key={division} value={division}>
                          {division}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Range</label>
                  <Select
                    value={selectedRange}
                    onValueChange={handleRangeChange}
                    disabled={!selectedDivision || mapLoading}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select Range" />
                    </SelectTrigger>
                    <SelectContent>
                      {ranges.map((range) => (
                        <SelectItem key={range} value={range}>
                          {range}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Beat</label>
                  <Select
                    value={selectedBeat}
                    onValueChange={setSelectedBeat}
                    disabled={!selectedRange || mapLoading}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select Beat" />
                    </SelectTrigger>
                    <SelectContent>
                      {beats.map((beat) => (
                        <SelectItem key={beat.name} value={beat.name}>
                          {beat.name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>

              {/* Filtered KPIs */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                <Card className="hover:shadow-md transition-shadow">
                  <CardHeader className="flex flex-row items-center justify-between pb-2">
                    <CardTitle className="text-sm font-medium">Total Observations</CardTitle>
                    <Activity className="h-4 w-4 text-muted-foreground" />
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">{filteredStats?.total_observations || 0}</div>
                    <div className="flex items-center gap-2 mt-1">
                      <Badge variant="secondary" className="text-xs">
                        {filteredStats?.today_observations || 0} today
                      </Badge>
                    </div>
                  </CardContent>
                </Card>

                <Card className="hover:shadow-md transition-shadow">
                  <CardHeader className="flex flex-row items-center justify-between pb-2">
                    <CardTitle className="text-sm font-medium">Direct Sightings</CardTitle>
                    <Eye className="h-4 w-4 text-muted-foreground" />
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">{filteredStats?.direct_sightings || 0}</div>
                    <div className="flex items-center gap-2 mt-1">
                      <Badge variant="secondary" className="text-xs">
                        {filteredStats?.today_direct_sightings || 0} today
                      </Badge>
                    </div>
                  </CardContent>
                </Card>

                <Card className="hover:shadow-md transition-shadow">
                  <CardHeader className="flex flex-row items-center justify-between pb-2">
                    <CardTitle className="text-sm font-medium">Indirect Signs</CardTitle>
                    <Footprints className="h-4 w-4 text-muted-foreground" />
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">{filteredStats?.indirect_sightings || 0}</div>
                    <div className="flex items-center gap-2 mt-1">
                      <Badge variant="secondary" className="text-xs">
                        {filteredStats?.today_indirect_sightings || 0} today
                      </Badge>
                    </div>
                  </CardContent>
                </Card>

                <Card className="hover:shadow-md transition-shadow">
                  <CardHeader className="flex flex-row items-center justify-between pb-2">
                    <CardTitle className="text-sm font-medium">Loss Reports</CardTitle>
                    <AlertTriangle className="h-4 w-4 text-muted-foreground" />
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">{filteredStats?.total_loss || 0}</div>
                    <div className="flex items-center gap-2 mt-1">
                      <Badge variant="secondary" className="text-xs">
                        {filteredStats?.today_loss || 0} today
                      </Badge>
                    </div>
                  </CardContent>
                </Card>
              </div>

              <div className="h-[600px] w-full rounded-lg border">
                <BeatMap
                  selectedBeat={selectedBeat}
                  selectedRange={selectedRange}
                  selectedDivision={selectedDivision}
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}; 