import React, { useEffect, useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { useNavigate } from "react-router-dom";
import { Users, FileText, Activity, Settings, AlertCircle, BarChart3 } from "lucide-react";
import { supabase } from "@/lib/supabaseClient";

export default function Admin() {
  const navigate = useNavigate();
  const [userCount, setUserCount] = useState<number | null>(null);
  const [observationCount, setObservationCount] = useState<number | null>(null);

  useEffect(() => {
    async function fetchCounts() {
      const { count: userCount } = await supabase
        .from("users")
        .select("*", { count: "exact", head: true });
      setUserCount(userCount ?? 0);

      const { count: obsCount } = await supabase
        .from("activity_observation")
        .select("*", { count: "exact", head: true });
      setObservationCount(obsCount ?? 0);
    }
    fetchCounts();
  }, []);

  const primaryCards = [
    {
      title: "User Management",
      description: "Add, edit, or remove users. Assign roles and manage permissions.",
      icon: <Users className="w-10 h-10 text-green-600" />,
      onClick: (navigate: any) => navigate('/admin/users'),
      color: "bg-white hover:bg-gray-50",
      stats: userCount !== null ? `${userCount} users` : "Loading...",
      link: "/admin/users"
    },
    {
      title: "Observation & Report Management",
      description: "View, edit, or delete observations and reports submitted by users.",
      icon: <FileText className="w-10 h-10 text-blue-600" />,
      onClick: (navigate: any) => navigate('/admin/observations'),
      color: "bg-white hover:bg-gray-50",
      stats: observationCount !== null ? `${observationCount} records` : "Loading...",
      link: "/admin/observations"
    }
  ];

  const secondaryCards = [
    {
      title: "Statistics & Analytics",
      description: "View detailed analytics and insights about system usage and activities.",
      icon: <BarChart3 className="w-8 h-8 text-purple-600" />,
      onClick: (navigate: any) => navigate('/admin/statistics'),
      color: "bg-white hover:bg-gray-50",
      stats: "87% active",
      link: "/admin/statistics"
    },
    {
      title: "Alerts & Notifications",
      description: "Manage system alerts and notification settings.",
      icon: <AlertCircle className="w-8 h-8 text-orange-600" />,
      onClick: (navigate: any) => navigate('/admin/alerts'),
      color: "bg-white hover:bg-gray-50",
      stats: "5 active",
      link: "/admin/alerts"
    },
    {
      title: "Settings",
      description: "Configure system settings and preferences.",
      icon: <Settings className="w-8 h-8 text-gray-600" />,
      onClick: (navigate: any) => navigate('/admin/settings'),
      color: "bg-white hover:bg-gray-50",
      stats: "Configure",
      link: "/admin/settings"
    }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100 py-12">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 mb-3">Admin Panel</h1>
          <p className="text-gray-600 text-lg">Manage your application's core functions and settings</p>
        </div>

        {/* Primary Cards - Larger size */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8 max-w-7xl mx-auto">
          {primaryCards.map((card) => (
            <Card
              key={card.title}
              className={`shadow-lg cursor-pointer transition-all duration-300 rounded-xl overflow-hidden ${card.color} hover:shadow-xl`}
              onClick={() => card.onClick(navigate)}
            >
              <CardHeader className="pb-2">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-4">
                    <div className="p-3 bg-gray-50 rounded-xl">
                      {card.icon}
                    </div>
                    <CardTitle className="text-2xl font-semibold text-gray-900">{card.title}</CardTitle>
                  </div>
                  <span className="text-sm font-medium text-gray-500 bg-gray-50 px-3 py-1 rounded-full">
                    {card.stats}
                  </span>
                </div>
              </CardHeader>
              <CardContent>
                <p className="text-gray-600 text-base">
                  {card.description}
                </p>
                <div className="mt-6 flex justify-end">
                  <span className="text-sm font-medium text-green-600 hover:text-green-700 flex items-center gap-1">
                    View Details
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </span>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Secondary Cards - Smaller size */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 max-w-7xl mx-auto">
          {secondaryCards.map((card) => (
            <Card
              key={card.title}
              className={`shadow-lg cursor-pointer transition-all duration-300 rounded-xl overflow-hidden ${card.color} hover:shadow-xl`}
              onClick={() => card.onClick(navigate)}
            >
              <CardHeader className="pb-2">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div className="p-2 bg-gray-50 rounded-lg">
                      {card.icon}
                    </div>
                    <CardTitle className="text-xl font-semibold text-gray-900">{card.title}</CardTitle>
                  </div>
                  <span className="text-sm font-medium text-gray-500 bg-gray-50 px-2 py-1 rounded-full">
                    {card.stats}
                  </span>
                </div>
              </CardHeader>
              <CardContent>
                <p className="text-gray-600 text-sm">
                  {card.description}
                </p>
                <div className="mt-4 flex justify-end">
                  <span className="text-sm font-medium text-green-600 hover:text-green-700 flex items-center gap-1">
                    View Details
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </span>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}
