import { Bell } from 'lucide-react';
import { useEffect } from 'react';
import { supabase } from '../lib/supabase';
import { useNotificationStore } from '../stores/notificationStore';
import { Button } from './ui/button';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from './ui/popover';
import { NotificationPanel } from './NotificationPanel';

export function NotificationBell() {
  const { fetchNotifications, unreadCount } = useNotificationStore();

  useEffect(() => {
    console.log('NotificationBell: Initial fetch');
    fetchNotifications();
    
    // Set up real-time subscription for new notifications
    const channel = supabase
      .channel('notifications')
      .on(
        'postgres_changes',
        {
          event: '*', // Listen for all events (INSERT, UPDATE, DELETE)
          schema: 'public',
          table: 'notifications',
        },
        (payload) => {
          console.log('NotificationBell: Received real-time update', payload);
          fetchNotifications();
        }
      )
      .subscribe((status) => {
        console.log('NotificationBell: Subscription status', status);
      });

    return () => {
      console.log('NotificationBell: Cleaning up subscription');
      supabase.removeChannel(channel);
    };
  }, [fetchNotifications]);

  return (
    <Popover>
      <PopoverTrigger asChild>
        <Button
          variant="ghost"
          size="icon"
          className="relative"
          aria-label="View notifications"
        >
          <Bell className="h-5 w-5" />
          {unreadCount > 0 && (
            <span className="absolute -right-1 -top-1 flex h-4 w-4 items-center justify-center rounded-full bg-red-500 text-[10px] font-medium text-white">
              {unreadCount}
            </span>
          )}
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-80 p-0" align="end">
        <NotificationPanel />
      </PopoverContent>
    </Popover>
  );
} 