import React, { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "../components/ui/card";
import { Button } from "../components/ui/button";
import { Input } from "../components/ui/input";
import { Label } from "../components/ui/label";
import { toast } from "sonner";
import { createClient } from "@supabase/supabase-js";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "../components/ui/table";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "../components/ui/select";
import { Switch } from "../components/ui/switch";

const supabase = createClient(
  import.meta.env.VITE_SUPABASE_URL,
  import.meta.env.VITE_SUPABASE_ANON_KEY
);

interface Alert {
  id: string;
  name: string;
  description: string;
  type: string;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

const AdminAlerts = () => {
  const [alerts, setAlerts] = useState<Alert[]>([]);
  const [loading, setLoading] = useState(true);
  const [newAlert, setNewAlert] = useState({
    name: "",
    description: "",
    type: "info",
    is_active: true,
  });

  useEffect(() => {
    fetchAlerts();
  }, []);

  const fetchAlerts = async () => {
    try {
      const { data, error } = await supabase
        .from("alerts")
        .select("*")
        .order("created_at", { ascending: false });

      if (error) throw error;
      setAlerts(data || []);
    } catch (error) {
      console.error("Error fetching alerts:", error);
      toast.error("Failed to fetch alerts");
    } finally {
      setLoading(false);
    }
  };

  const handleCreateAlert = async () => {
    try {
      const { error } = await supabase.from("alerts").insert([newAlert]);

      if (error) throw error;

      toast.success("Alert created successfully");
      setNewAlert({
        name: "",
        description: "",
        type: "info",
        is_active: true,
      });
      fetchAlerts();
    } catch (error) {
      console.error("Error creating alert:", error);
      toast.error("Failed to create alert");
    }
  };

  const handleToggleAlert = async (alertId: string, currentStatus: boolean) => {
    try {
      const { error } = await supabase
        .from("alerts")
        .update({ is_active: !currentStatus })
        .eq("id", alertId);

      if (error) throw error;

      toast.success("Alert status updated");
      fetchAlerts();
    } catch (error) {
      console.error("Error updating alert:", error);
      toast.error("Failed to update alert status");
    }
  };

  const handleDeleteAlert = async (alertId: string) => {
    try {
      const { error } = await supabase.from("alerts").delete().eq("id", alertId);

      if (error) throw error;

      toast.success("Alert deleted successfully");
      fetchAlerts();
    } catch (error) {
      console.error("Error deleting alert:", error);
      toast.error("Failed to delete alert");
    }
  };

  return (
    <div className="container mx-auto py-6">
      <h1 className="text-3xl font-bold mb-6">Alert Management</h1>

      {/* Create New Alert */}
      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Create New Alert</CardTitle>
          <CardDescription>Configure a new alert for the system</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="alertName">Alert Name</Label>
              <Input
                id="alertName"
                value={newAlert.name}
                onChange={(e) =>
                  setNewAlert({ ...newAlert, name: e.target.value })
                }
                placeholder="Enter alert name"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="alertType">Alert Type</Label>
              <Select
                value={newAlert.type}
                onValueChange={(value) =>
                  setNewAlert({ ...newAlert, type: value })
                }
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select alert type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="info">Information</SelectItem>
                  <SelectItem value="warning">Warning</SelectItem>
                  <SelectItem value="error">Error</SelectItem>
                  <SelectItem value="success">Success</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
          <div className="space-y-2">
            <Label htmlFor="alertDescription">Description</Label>
            <Input
              id="alertDescription"
              value={newAlert.description}
              onChange={(e) =>
                setNewAlert({ ...newAlert, description: e.target.value })
              }
              placeholder="Enter alert description"
            />
          </div>
          <div className="flex items-center space-x-2">
            <Switch
              id="alertStatus"
              checked={newAlert.is_active}
              onCheckedChange={(checked) =>
                setNewAlert({ ...newAlert, is_active: checked })
              }
            />
            <Label htmlFor="alertStatus">Active</Label>
          </div>
          <Button onClick={handleCreateAlert}>Create Alert</Button>
        </CardContent>
      </Card>

      {/* Alerts List */}
      <Card>
        <CardHeader>
          <CardTitle>Active Alerts</CardTitle>
          <CardDescription>Manage existing alerts in the system</CardDescription>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div>Loading alerts...</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Name</TableHead>
                  <TableHead>Type</TableHead>
                  <TableHead>Description</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Created</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {alerts.map((alert) => (
                  <TableRow key={alert.id}>
                    <TableCell>{alert.name}</TableCell>
                    <TableCell>
                      <span
                        className={`px-2 py-1 rounded-full text-xs ${
                          alert.type === "error"
                            ? "bg-red-100 text-red-800"
                            : alert.type === "warning"
                            ? "bg-yellow-100 text-yellow-800"
                            : alert.type === "success"
                            ? "bg-green-100 text-green-800"
                            : "bg-blue-100 text-blue-800"
                        }`}
                      >
                        {alert.type}
                      </span>
                    </TableCell>
                    <TableCell>{alert.description}</TableCell>
                    <TableCell>
                      <Switch
                        checked={alert.is_active}
                        onCheckedChange={() =>
                          handleToggleAlert(alert.id, alert.is_active)
                        }
                      />
                    </TableCell>
                    <TableCell>
                      {new Date(alert.created_at).toLocaleDateString()}
                    </TableCell>
                    <TableCell>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => handleDeleteAlert(alert.id)}
                      >
                        Delete
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default AdminAlerts; 