import { create } from 'zustand';
import { supabase } from '../lib/supabase';

interface Notification {
  id: string;
  user_id: string;
  activity_id: string;
  division_id: string;
  range_id: string | null;
  message: string;
  is_read: boolean;
  created_at: string;
  updated_at: string;
}

interface NotificationStore {
  notifications: Notification[];
  unreadCount: number;
  isLoading: boolean;
  error: string | null;
  fetchNotifications: () => Promise<void>;
  markAsRead: (notificationId: string) => Promise<void>;
  markAllAsRead: () => Promise<void>;
}

export const useNotificationStore = create<NotificationStore>()((set) => ({
  notifications: [],
  unreadCount: 0,
  isLoading: false,
  error: null,

  fetchNotifications: async () => {
    console.log('NotificationStore: Starting fetch');
    set({ isLoading: true, error: null });
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        console.log('NotificationStore: No authenticated user');
        throw new Error('No authenticated user');
      }
      console.log('NotificationStore: Fetching notifications for user', user.id);

      const { data, error } = await supabase
        .from('notifications')
        .select('*')
        .eq('user_id', user.id)
        .order('created_at', { ascending: false });

      if (error) {
        console.error('NotificationStore: Error fetching notifications', error);
        throw error;
      }

      console.log('NotificationStore: Fetched notifications', data);
      set({
        notifications: data,
        unreadCount: data.filter((n) => !n.is_read).length,
        isLoading: false,
      });
    } catch (error) {
      console.error('NotificationStore: Error in fetchNotifications', error);
      set({ error: (error as Error).message, isLoading: false });
    }
  },

  markAsRead: async (notificationId: string) => {
    console.log('NotificationStore: Marking notification as read', notificationId);
    try {
      const { error } = await supabase
        .from('notifications')
        .update({ is_read: true })
        .eq('id', notificationId);

      if (error) {
        console.error('NotificationStore: Error marking notification as read', error);
        throw error;
      }

      set((state) => ({
        notifications: state.notifications.map((n) =>
          n.id === notificationId ? { ...n, is_read: true } : n
        ),
        unreadCount: state.unreadCount - 1,
      }));
    } catch (error) {
      console.error('NotificationStore: Error in markAsRead', error);
      set({ error: (error as Error).message });
    }
  },

  markAllAsRead: async () => {
    console.log('NotificationStore: Marking all notifications as read');
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) {
        console.log('NotificationStore: No authenticated user');
        throw new Error('No authenticated user');
      }

      const { error } = await supabase
        .from('notifications')
        .update({ is_read: true })
        .eq('user_id', user.id)
        .eq('is_read', false);

      if (error) {
        console.error('NotificationStore: Error marking all notifications as read', error);
        throw error;
      }

      set((state) => ({
        notifications: state.notifications.map((n) => ({ ...n, is_read: true })),
        unreadCount: 0,
      }));
    } catch (error) {
      console.error('NotificationStore: Error in markAllAsRead', error);
      set({ error: (error as Error).message });
    }
  },
})); 